import phoneList from './phone-list.vue'
import phoneAlphabet from './phone-alphabet.vue'
export default {
	name: "phone-directory",
	components: {
		phoneList,
		phoneAlphabet,
	},
	props: {

		phones: {
			type: Object

		},


		default: false
	},
	data() {
		return {
			isSect: true,
			winHeight: 0,
			letter: 'A',
			scrollAnimationOFF: true,
			phoneListIndex: 'A',
			reset: true
		}
	},
	computed: {
		phonesEscape() {
			return escape(JSON.stringify(this.phones))
		}
	},
	mounted() {


		let windowHeight = uni.getSystemInfoSync().windowHeight

		this.winHeight = windowHeight






		if (!this.phones) {
			uni.showToast({
				title: '没有数据',
				icon: "none",
				mask: false,
				duration: 1500
			})
		}
	},
	methods: {
		goSecletCity() {
			//如果选择城市
			uni.navigateTo({
				url: '../../pages/local/cityList'
			})
		},

		handleClick(e) {
			this.$emit('paramClick', e)
		},
		handleDatasetKey(val) {
			this.letter = val
		},
		handleScrollAnimationOFF(val) {
			this.scrollAnimationOFF = val
		},
		handlePhoneListIndex(val) {
			if (this.reset) {
				this.phoneListIndex = val
			}
		},
		handleReset(val) {
			if (val) {
				this.letter = ''
			}
			this.reset = val
		}

	}
}
